class e{constructor(){this.copyright="",this.defaultScene=0,this.generator="",this._scenes=[]}addScene(e){if(this._scenes.indexOf(e)>=0)throw new Error("Scene already added");this._scenes.push(e)}removeScene(e){const s=this._scenes.indexOf(e);s>=0&&this._scenes.splice(s,1)}forEachScene(e){this._scenes.forEach(e)}}export{e as Asset};
